@import "src/styles/variables";
@import "src/styles/mixins";

.note {
  padding: 10px 15px;
  background-color: $note-bg-color;
  border-radius: 5px;
  margin-bottom: 8px;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
  font-size: 14px;

  button {
    @include btn;
    border: $border-thin;
    padding: 0 5px;
    height: 30px;
    width: 100%;
  }

  button:hover {
    .icon {
      opacity: 1;
    }
  }

  .remove-btn {
    margin-bottom: 10px;
    position: relative;
    width: 30px;

    .icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.note-content {
  width: 100%;
  word-break: break-word;
}

.buttons {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}

.icon {
  @include icon(10px, 0.5);
}

.like-btn {
  display: flex;
  align-items: center;

  .liked-icon {
    opacity: 1;
  }

  .likes-amount {
    padding: 0 10px 0 5px;
    font-size: 15px;
  }
}
